<template>
  <div id="video">
    <!-- <div  v-if="videos.length>0"> -->
    <ul class="box" v-if="videos.length > 0">
      <li class="item" v-for="item in videos" :key="item.id" @click="pushDetail(item.id)">
        <div class="header">
          <img class="img" :src="item.img" alt="" />
          <i class="icon el-icon-video-camera">
            <span>{{ item.count }}</span></i
          >
          <div class="time">
            <span>{{ item.time | dateFormat('MM:SS') }}</span>
          </div>
          <i class="contentIcon el-icon-video-camera"></i>
        </div>
        <div class="box">
          <div class="title ellipsis">{{ item.title }}</div>
          <div class="creator">{{ item.creator }}</div>
        </div>
      </li>
    </ul>

    <div v-if="loadStatus" class="load-bottom">
      <text-loading />
    </div>

    <div v-else>哎呀，还没写呢</div>
  </div>
</template>

<script>
export default {
  props: ['videos', 'loadStatus'],
  created() {
    console.log(this.videos)
  },
  methods: {
    pushDetail(id) {
      console.log()
      if (typeof id == 'number' || id.length < 10) {
        this.$router.push({
          path: '/mvDetail',
          query: {
            id
          }
        })
      } else {
        this.$router.push({
          path: '/videoDetail',
          query: {
            id
          }
        })
      }
    }
  }
}
</script>

<style scoped lang="less">
#video {
 
  margin: 0 -10px;
  margin-top: 20px;
.box{
   display: flex;
  flex-wrap: wrap;
}
  .item {
    width: 25%;
    padding: 0 10px 20px;
    .header:hover {
      .contentIcon {
        display: block;
      }
    }
    .header {
      position: relative;
      color: #fff;
      font-size: 14px;
      .img {
        width: 100%;
      }
      .icon {
        height: 20px;
        width: 25%;
        line-height: 20px;
        position: absolute;
        top: 0;
        right: 0;

        background: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.4));
        font-size: 14px;
        span {
          font-size: 12px;
          float: right;
          margin-right: 8px;
        }
      }
      .time {
        height: 20px;
        width: 100%;
        line-height: 20px;
        position: absolute;
        bottom: 4px;
        right: 0;
        background: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.4));

        span {
          float: right;
          margin-right: 8px;
        }
      }
      .contentIcon {
        position: absolute;
        top: 50%;
        left: 50%;
        font-size: 30px;
        color: #ec0801;
        transform: translate(-50%, -50%);
        display: none;
      }
    }
    .box {
      .title {
        margin-top: 5px;
        font-size: 14px;
        font-weight: 700;
      }
      .creator {
        font-size: 10px;
        margin-top: 10px;
        font-weight: 600;
      }
    }
  }
}
</style>
