<template>
  <div class="song" v-if="albums">
      <div class="list " v-for="item in albums" :key="item.id" @click="toDetail(item.id)">
        <img :src="item.picUrl|| item.coverImgUrl" alt="" />
        <div class="ellipsis-two ">{{ item.name }}</div>
        <div class="icon"><img src="@/assets/img/recommend/erji.svg" alt="" />{{ item.playCount ?item.playCount:item.type||item.subType?item.subType:item.type}}</div>
      </div>
    </div>
</template>

<script>
export default {
    props:['albums'],
 methods: {
    toDetail (id) {
      this.$router.push({
        path: '/albumDetails',
        query: {
          id
        }
      }
      )
    }
  }
}
</script>


