import { axios } from './axios'

// 全部歌手
export function singer(params) {
  return axios({
    url: '/artist/list',
    params: {
      area: params.area,
      type: params.type,
      limit: params.limit,
      initial: params.initial,
      offset: params.offset
    }
  })
}

// 歌手全部歌曲
export function allSong(id) {
  return axios({
    url: '/artist/songs',
    params: {
      id
    }
  })
}
// 获取歌手描述
export function getArtistDescDetail(id) {
  return axios({
    url: '/artist/desc',
    params: {
      id
    }
  })
}
// 获得歌手基本信息和歌曲
export function getSingerSong(id) {
  return axios({
    url: '/artists',
    params: {
      id
    }
  })
}
// 获得歌手专辑
export function getSingerAlbum(params) {
         return axios({
           url: '/artist/album',
           params: {
             id: params.id,
             limit: params.limit,
             offset: params.offset
           }
         })
       }
// 获得歌手mv信息
export function getMvInfo(params) {
  return axios({
    url: '/artist/mv',
    params: {
      id: params.id,
      limit: params.limit,
      offset: params.offset
    }
  })
}
