<template>
  <div class="singer-detail">
    <div class="top">
      <div class="mask">
        <div class="info">
          <div class="avatar">
            <img :src="artist.img1v1Url" alt="" />
            <div class="name">{{ artist.name }} <i class="iconfont gender-info" :class="gender"></i></div>
            <div class="follow">
              <i class=" iconfont icon-guanzhu"></i>
              关注TA
            </div>
            <div class="desc ellipsis-two" v-if="artist.briefDesc">
              {{ artist.briefDesc }}
            </div>
            <ul class="statistics">
              <li>
                <span class="num">{{ artist.musicSize }}</span>
                <p class="text">单曲</p>
              </li>
              <li>
                <span class="num">{{ artist.albumSize }}</span>
                <p class="text">专辑</p>
              </li>
              <li>
                <span class="num">{{ artist.mvSize }}</span>
                <p class="text">MV</p>
              </li>
              <li>
                <span class="num">{{ userDeatl.followeds }}</span>
                <p class="text">粉丝</p>
              </li>
            </ul>
          </div>
        </div>
      </div>

      <div class="botton-bg"></div>
    </div>
    <div class="botton limit">
      <ul class="nav">
        <li v-for="item in navList" :key="item.id" @click="tabItem(item.id)" :class="{ active: active === item.id }">{{ item.name }}</li>
      </ul>
      <div class="list">
        <songs-list :songs="songs" v-if="active === 1"></songs-list>
        <albums :albums="albums" v-if="active === 2"></albums>
        <mv-list :videos="mvs" v-if="active === 3"></mv-list>
      </div>
    </div>
    <loading v-show="isLoading"></loading>

  </div>
</template>

<script>
import { getArtistDescDetail, getSingerSong, getSingerAlbum, getMvInfo } from '@/network/singer'
import { getUserDetail } from '@/network/login'
import { SongsInfo, videoInfo } from '@/network/detail'
import { mapGetters } from 'vuex'

import songsList from '@/components/common/ArtistList/ArtistList'
import albums from '@/components/common/albums/albums'
import mvList from '@/components/common/videos/video'
export default {
  components: {
    songsList,
    albums,
    mvList
  },
  data() {
    return {
      // 歌手信息
isLoading:true,
      artist: {},
      desc: '',
      songs: {},
      userDeatl: {},
      navList: [
        {
          name: '作品',
          id: 1
        },
        {
          name: '专辑',
          id: 2
        },
        {
          name: 'MV',
          id: 3
        },
        {
          name: '歌手详情',
          id: 4
        },
        {
          name: '相似歌手',
          id: 5
        }
      ],
      // 显示tab
      active: 1,
      albums: {},
      offset: 0,
      mvs:[]
    }
  },
  watch: {
    $route() {}
  },
  computed: {
    ...mapGetters(['singerInfo']),
    // 设置性别
    gender() {
      if (this.userDeatl.gender > 0) {
        return this.userDeatl.gender === 1 ? 'icon-nan man' : 'icon-nv woman'
      } else {
        return ''
      }
    }
  },
  created() {
    let id = this.$route.query.id || this.singerInfo.id
    if (id) {
      this.init(id)
    }
  },
  methods: {
    init(id) {
      this.getDes(id)
      this.singerUserInfo(id)
      this.getSingerAlbum(id)
      
    },
    getDes(id) {
      getArtistDescDetail(id).then(res => {
        this.desc = res.data.briefDesc
      })
    },
    // 获取歌手基本信息和歌曲
    singerUserInfo(id) {
      getSingerSong(id).then(res => {
        this.artist = res.data.artist
        this.getUserDetail(res.data.artist.accountId)
        let data = []
        res.data.hotSongs.forEach(item => {
          data.push(new SongsInfo(item))
        })
        this.songs = data
        this.getMvInfo(id)
      })
    },
    // 获用户信息
    getUserDetail(id) {
      getUserDetail(id).then(res => {
        let detail = {}
        let profile = res.data.profile
        detail.followeds = profile.followeds
        detail.gender = profile.gender
        detail.level = res.data.level
        this.userDeatl = detail
        this.isLoading=false

      })
    },
    tabItem(id) {
      this.active = id
    },
    // 获得专辑
    getSingerAlbum(id) {
      let params = {
        id: id,
        limit: this.artist.albumSize,
        offset: this.offset
      }
      getSingerAlbum(params).then(res => {
        this.albums = res.data.hotAlbums
      })
    },
    // 获得Mv信息
    getMvInfo(id) {
      console.log(this.artist.mvSize);
       let params = {
        id: id,
        limit: this.artist.mvSize,
        offset: this.offset
      }
      getMvInfo(params).then(res => {
        let mvs=[]
        res.data.mvs.forEach(item=>{
          mvs.push(new videoInfo(item))
        })
        this.mvs=mvs
        console.log(this.mvs);
      })
    }
  }
}
</script>

<style scoped lang="less">
.singer-detail {
  width: 100%;
}
.top {
  position: relative;
  height: 580px;
  background: url('../../../assets/img/singer/bg.jpg') no-repeat;
  background-size: cover;
  .mask {
    position: relative;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    .info {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      .avatar {
        display: flex;
        flex-direction: Column;
        align-items: center;
        img {
          width: 120px;
          height: 120px;
          border-radius: 50%;
        }
        .name {
          margin: 15px 0;
          font-size: 25px;
          font-weight: 600;
          color: #fff;
          i {
            margin-left: 5px;
            font-size: 20px;
          }
          .man {
            color: #1f91f2;
          }
          .woman {
            color: #fc7265;
          }
        }
        .follow {
          height: 36px;
          width: 96px;
          line-height: 36px;
          text-align: center;
          margin-bottom: 22px;
          font-size: 14px;
          color: #fff;
          border: 1px solid rgba(255, 255, 255, 0.6);
          border-radius: 20px;
          cursor: pointer;
          i {
            font-size: 10px;
          }
        }
        .follow:hover {
          background: rgba(255, 255, 255, 0.1);
          border: 1px solid #fff;
        }
        .desc {
          margin: 0 auto 15px;
          width: 740px;
          height: 48px;
          font-size: 14px;
          color: rgba(255, 255, 255, 0.6);
          line-height: 24px;
        }
        .statistics {
          display: flex;
          li {
            margin: 50px 50px -50px;
          }
          .num {
            color: #fff;
            font-size: 25px;
            font-weight: 700;
          }
          .text {
            text-align: center;
            font-size: 14px;
            line-height: 14px;
            color: rgba(255, 255, 255, 0.6);
          }
        }
      }
    }
  }
  .botton-bg {
    position: absolute;
    bottom: 0;
    height: 130px;
    width: 100%;
    background: url('../../../assets/img/singer/botton.png');
    background-position: center;
    z-index: 2;
  }
}
.botton {
  .nav {
    display: flex;
    justify-content: center;
    li {
      margin: 50px 30px 0;
      font-weight: 600;
      font-size: 18px;
    }
  }
  .active {
    color: red;
  }
}
</style>
